export default {
  name: 'ResultBall',
  props: {
    gameCode: {
      type: String
    },
    resultballs: {
      type: Array,
      default: []
    },
    count: {
      type: Object
    },
    baseNumber: {
      type: Number
    }
  },
  computed: {
    loading () {
      if (this.count) {
        return this.count.days + this.count.days + this.count.minutes + this.count.seconds === 0
      }
      return false
    }
  },
  methods: {
    getPositiveNumber (baseNumber, num, flag) {
      let diff = Math.abs(baseNumber - num)
      return flag || diff ? diff : baseNumber
    }
  }
}



